import Component from '../React/component';

const creatComponent = (comp, props) => {
  let inst;
  //  Âà§Êñ≠ÊòØÁ±ªÁªÑ‰ª∂ËøòÊòØÂáΩÊï∞ÁªÑ‰ª∂
  if (comp.prototype && comp.prototype.render) {
    //Á±ªÁªÑ‰ª∂
    inst = new comp(props);
  } else {
    //Â¶ÇÊûúÊòØÂáΩÊï∞,Â∞ÜÂáΩÊï∞ÁªÑ‰ª∂Êâ©Â±ïÊàêÁ±ªÁªÑ‰ª∂,Êñπ‰æøÂêéÈù¢Áªü‰∏ÄÂ§ÑÁêÜ
    inst = new Component(props);

    inst.construct = comp; //Â∞ÜÊûÑÈÄ†ÁöÑÂØπË±°constructionÁöÑÂ±ûÊÄßÊåáÂêë‰º†ËøõÊù•ÁöÑÂáΩÊï∞
    inst.render = () => inst.construct(props);
  }
  return inst;
};
const setComponentProps = (comp, props) => {
  //ËÆæÁΩÆÂ±ûÊÄß
  comp.props = props;
  //Ê∏≤ÊüìËäÇÁÇπ
  renderComponent(comp);
};
const renderComponent = (comp) => {
  let base;
  const rendered = comp.render();//ËøîÂõûjsxÂØπË±°
  base = _render(rendered);//ËøîÂõûËäÇÁÇπÂØπË±°
  console.log('base',base)
  comp.base = base;
};
const _render = (vNode) => {
  // Âà§Êñ≠ËäÇÁÇπÁ±ªÂûã
  if (!vNode || typeof vNode === 'boolean') {
    return;
  }
  if (typeof vNode === 'string') {
    // Â≠óÁ¨¶‰∏≤Á±ªÂûã
    return document.createTextNode(vNode);
  }
  //ËôöÊãüdomÁ±ªÂûã
  const { tag, attr, children } = vNode;
  // Âà§Êñ≠tagÁ±ªÂûã
  if (tag) {
    if (typeof tag === 'function') {
      //Â¶ÇÊûútagÊòØÂáΩÊï∞
      // 1.ÂàõÂª∫ÁªÑ‰ª∂
      const comp = creatComponent(tag, attr);
      console.log('comp', comp);
      // 2.ËÆæÁΩÆÁªÑ‰ª∂ÁöÑÂ±ûÊÄß
      setComponentProps(comp, attr);
      // 3.ÁªÑ‰ª∂Ê∏≤ÊüìÁöÑËäÇÁÇπÂØπË±°ËøîÂõû
      return comp.base;
    }
    //ÂàõÂª∫ËäÇÁÇπÂØπË±°
    const dom = document.createElement(tag);
    if (attr) {
      // ÂØπËäÇÁÇπÁöÑÂ±ûÊÄßËøõË°åÂæ™ÁéØ
      Object.keys(attr).forEach((key) => {
        const value = attr[key];
        setAttributeFunc(dom, key, value); //ËÆæÁΩÆÂ±ûÊÄß
      });
    }
    //Ê∏≤ÊüìÂ≠êËäÇÁÇπ
    if (children) {
      children.forEach((child) => render(child, dom)); // ÈÄíÂΩíÊ∏≤Êüì
    }
    return dom;
  }
};
const render = (node, container) => {
  return container.appendChild(_render(node));
};
const setAttributeFunc = (dom, key, value) => {
  // Âà§Êñ≠keyÁöÑÁ±ªÂûã
  // Â¶ÇÊûúkeyÊòØÁ±ªÂêçÁõ¥Êé•ËΩ¨Êç¢Êàêclass
  if (key === 'className') {
    key = 'class';
  }
  // 1ÊòØ‰∫ã‰ª∂ Â¶ÇonClick,onBlur...
  if (/on\w+/.test(key)) {
    key = key.toLowerCase();
    dom[key] = value[key] || '';
  } else if (key === 'style') {
    // 2ÊòØÊ†∑Âºè style
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '';
    } else if (typeof value === 'object') {
      //Ê†∑ÂºèÊòØÂØπË±°
      for (const k in value) {
        //Âæ™ÁéØÊ†∑Âºè
        if (typeof k === 'number') {
          dom.style[k] = value[k] + 'px';
        } else {
          dom.style[k] = value[k];
        }
      }
    }
  } else {
    // 3ÂÖ∂‰ªñÂ±ûÊÄß
    if (key in dom) {
      dom[key] = value || '';
    }
    if (value) {
      dom.setAttribute(key, value);
    } else {
      dom.removeAttribute(key);
    }
  }
};

const ReactDOM = {
  render,
};
export default ReactDOM;
